.dressContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.container {
  position: relative;
  margin-bottom: 20px;
  width: 320px;
}

.image {
  display: block;
  width: 100%;
  height: 100%;
}

.img-logo-collections{
    height: 120px;
    width: 120px;
    margin: 0 auto;
    /* margin-top: -2%; */
    /* margin-bottom: 5%; */
    }

.overlay {
  position: absolute;
  bottom: 0;
  background-color: #C9954C ;
  overflow: hidden;
  width: 290px;
  height: 0;
  -webkit-transition: .2s ease;
  -o-transition: .2s ease;
  transition: .2s ease;
}

.collection-name{
    color: #C9954C;
    font-family: 'Bellefair', serif;
    padding: 10px; 
}

.collection-description{
    color: #C9954C;
    font-family: 'Bellefair', serif;
    padding-bottom: 100px;
}

.container:hover .overlay {
  height: 10%;
}

.text {
  white-space: nowrap;
  color: #0A0C26;
  font-size: 20px;
  position: absolute;
  overflow: hidden;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}



